<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import MyInput from '../components/Input.vue'
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import editStyle from '../components/editStyle' // 

// [[]]  v push concat [].concat([v])
// [[v]]  v, [[v, v]],  [[v], [v2]]

export default {
  name: 'home',
  data () {
    return {
      greenTemplate: false, // 
      editStyle: false, // 
      nowShowInGreenTemplate: [], // greenTemplate  
      contentType: 1, // bar  
      home_height: 0,
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        }
      },
      selectGoods: null // 
    }
  },
  components: {
    swiper,
    swiperSlide,
    editStyle
  },
  methods: {
    // editStyle 
    acceptData (data) {
      this.selectGoods = data
    },
    //  greenTemplate
    hideGreenTemplate () {
      this.greenTemplate = false
      this.nowShowInGreenTemplate.forEach(v => { this[v] = false })
    },
    //  greenTemplate
    showGreenTemplate (name, boolean) {
      this.greenTemplate = true
      this[name] = boolean
      this.$nextTick(() => {
        document.querySelector('.union-id-1 .styles .style').classList.add('select-style')
      })
    },
    // 
    getGoodsKind () {
      this.$root.showLoading = true
      this.$axios.get('/web/v1/cosleep/entitygoods/kind', { params: { goods_id: this.$root.goods_id } }).then(res => {
        if (res.data.status !== 1) {
          // console.log('')
          return
        }
        this.$root.showLoading = false
        let kinds = {
          '': [],
          '': [],
          '': []
        }
        const data = res.data.data
        data.forEach(v => {
          kinds[v.kind_style].push(v)
        })

        for (let i in kinds) {
          let total = 0
          kinds[i].forEach(v => {
            total += +v.kind_total_count
          })
          kinds[i].forEach(v => {
            v.total = total
          })
        }

        // console.log(this.$refs['editStyle'])
        this.$refs['editStyle'].kinds = kinds
        this.$refs['editStyle'].nowKind = kinds['']
      })
    },
    // 
    submit (boolean) {
      if (boolean) {
        this.$router.push(`/edit?id=${this.selectGoods.data.kind_id}&count=${this.selectGoods.count}`)
      } else {
        this.$parent.showTips('')
      }
    },
    contentTypeChange (type) {
      this.contentType = +type
    }
  },
  mounted () {
    this.getGoodsKind()
  }
}
</script>

