<template>
  <div class="users-address">
    <div class="input-template">
      <p class="title"></p>
      <input placeholder="" onkeyup="this.value=this.value.replace(/\s+/g,'')" v-model="inputData.receiver_name" class="input" />
    </div>
    <div class="input-template">
      <p class="title"></p>
      <input placeholder="" :change="check_num()" v-model="inputData.mobile" class="input" />
    </div>
    <div class="input-template" @click="show = true">
      <p class="title"></p>
      <p class="address">{{ alst(inputData.province + inputData.city + inputData.area) }}</p>
      <div class="arrow">></div>
    </div>
    <div class="input-template">
      <p class="title"></p>
      <textarea placeholder="" v-model="inputData.address" class="input text-area"></textarea>
    </div>

    <div class="meain-pad-color">

    </div>
    <!-- <div class="clasp animated fadeIn" v-if="show">
    </div> -->
    <div class="button-template">
      <div class="button" @click="submitData(inputData.mobile.length === 11 && inputData.city && inputData.address && inputData.receiver_name)" :class="{'background-blue': inputData.mobile.length === 11 && inputData.city && inputData.address && inputData.receiver_name}"></div>
    </div>
    <div @click.self="show = false" style="position: fixed;top: 0px;left:0px; height: 100vh;width: 100vw;background: rgba(0, 0, 0, 0.5)" v-show="show">
      <AreaP class="area" ref="area"></AreaP>
    </div>
  </div>
</template>

<script>
import AreaP from './area.vue'

export default {
  name: 'usersAddress',
  props: {
    msg: String
  },
  data () {
    return {
      show: false,
      inputData: {
        province: '',
        city: '',
        area: '',
        address: '',
        receiver_name: '',
        mobile: ''
      }
    }
  },
  methods: {
    // can though
    submitData (boolean) {
      if (boolean) {
        this.$parent.addressShow = false
        this.$parent.inputData = this.inputData
      } else {
        if (!this.inputData.receiver_name) {
          this.$parent.$parent.showTips('')
        }
        if (this.inputData.mobile.length !== 11) {
          this.$parent.$parent.showTips('')
          return
        }
        if (this.inputData.mobile.length === 0) {
          this.$parent.$parent.showTips('')
          return
        }
        if (!this.inputData.address) {
          this.$parent.$parent.showTips('')
          return
        }
        if (!this.inputData.city) {
          this.$parent.$parent.showTips('')
        }
      }
    },
    check_num () {
      let licenseNum = this.inputData.mobile
      licenseNum = (licenseNum + '').replace(/[^\d]/g, '') // .
      if (licenseNum.indexOf('.') < 0 && licenseNum !== '') {
        //  0102
        licenseNum = parseInt(licenseNum)
      }
      this.inputData.mobile = licenseNum.toString().slice(0, 11)
    },
    alst (desc) {
      /* eslint-disable */
      if (desc.replace(/\s/g, '')) {
        this.changeColor = 'rgba(0, 0, 0, 0.6)'
      }
      return desc.replace(/\s/g, '') ? desc : ''
    }
  },
  components: {
    AreaP
  }
}
</script>

