export default class wjhTool {
  static promiseForBoolean (obj = {}) {
    let interval; let promise; let save = {}
    Object.assign(save, obj)
    const func = (resolve) => {
      if (obj.boolean === !save.boolean) {
        clearInterval(interval)
        resolve()
      }
    }
    promise = new Promise((resolve, reject) => {
      interval = setInterval(func.bind(null, resolve), 1000)
    })
    return promise
  }

  static getAPIData (url, obj = {}, callback, arg = {}) {
    const api = new new XMLHttpRequest()
    let paramsString = ''

    api.open('GET', url)

    api.onreadystatechange = function () {
      if (this.readyState === 4 && this.status === 200) {
        if (callback) callback()
      }
    }

    api.setRequestHeader('Content-Type', 'application/json')

    for (let { val, key } of arg) {
      paramsString += key + '=' + val + '&'
    }

    paramsString.replace(/&$/, '')

    api.send()
  }

  static randomNum (minNum, maxNum) {
    switch (arguments.length) {
      case 1:
        return parseInt(Math.random() * minNum + 1, 10)
      case 2:
        return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10)
      default:
        return 0
    }
  }

  static musicCount (value) {
    var secondTime = parseInt(value)// 
    var minuteTime = 0// 
    var hourTime = 0// 
    if (secondTime > 60) { // 60
      // 60
      minuteTime = parseInt(secondTime / 60)
      // 
      secondTime = parseInt(secondTime % 60)
      // 60
      if (minuteTime > 60) {
        // 60
        hourTime = parseInt(minuteTime / 60)
        // 60
        minuteTime = parseInt(minuteTime % 60)
      }
    }
    let mark = ''
    if (parseInt(secondTime) < 10) {
      mark = '0'
    }
    var result = mark + parseInt(secondTime) + ' '

    let mark2 = ''
    if (parseInt(minuteTime) < 10) {
      mark2 = '0'
    }

    result = mark2 + parseInt(minuteTime) + ' : ' + result

    if (hourTime > 0) {
      result = '' + parseInt(hourTime) + '' + result
    }
    return result
  }

  static dateFormat (date, format) { // yy:mm:ss // yyyymmmmsss
    if (!format || typeof format !== 'string') {
      console.error('format is undefiend or type is Error')
      return ''
    }

    date = date instanceof Date ? date : (typeof date === 'number' || typeof date === 'string') ? new Date(date) : new Date()

    // 
    let formatReg = {
      'y+': date.getFullYear(),
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let reg in formatReg) {
      if (new RegExp(reg).test(format)) {
        let match = RegExp.lastMatch // 
        format = format.replace(match, formatReg[reg] < 10 ? '0' + formatReg[reg] : formatReg[reg].toString()) // formatReg[reg]< 10 ? '0'+formatReg[reg]:     2019031811:42:24 0
      }
    }
    return format
  }

  static judgeWeiXin () { //   qq
    let z = null
    if (window.navigator.userAgent.toLowerCase().match(/QQ/i)) {
      z = window.navigator.userAgent.toLowerCase().match(/QQ/i)[0]
    }
    if (!/micromessenger/.test(navigator.userAgent.toLowerCase()) && z !== 'qq') {
      return false
    } else {
      return true
    }
  }

  static deepCopy (obj) {
    let temp = obj.constructor === Array ? [] : {}
    for (let val in obj) {
      temp[val] = typeof obj[val] === 'object' ? wjhTool.deepCopy(obj[val]) : obj[val]
    }
    return temp
  }

  static deBounce (fn, wait = 1000) {
    let timeOut = null
    return () => {
      if (timeOut !== null) clearTimeout(timeOut)
      timeOut = setTimeout(fn, wait)
    }
  }

  static parseQuery (url) { //  params 
    let queryObj = {}
    let reg = /[?&]([^=&#]+)=([^&#]*)/g
    let querys = url.match(reg)
    if (querys) {
      for (let i in querys) {
        let query = querys[i].split('=')
        let key = query[0].substr(1)
        let value = query[1]
        queryObj[key] ? queryObj[key] = [].concat(queryObj[key], value) : queryObj[key] = value
      }
    }
    return queryObj
  }
}
